<template>
  <div class="mind">
    <div class="top-post-it-logo"></div>
    <div class="top-3m-logo"></div>
    <div class="mind__wrap">
      <transition name="fade">
        <div class="mind__card-wrap" v-if="isActive">
          <MindCard :questionData="minds" :questionLeveLIndex="questionLevel" :nextQuestion="nextQuestion" :subQuestionLevel="subQuestionLevel" />
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import MindCard from '../components/MindCard'
export default {
  name: 'Mind',
  components: {
    MindCard
  },
  data() {
    return {
      userScore: 0,
      questionLevel: 0,
      subQuestionLevel: 0,
      isActive: true,
      minds: [
        {
          questions: [
            {
              question: '好不容易放假了',
              answers: [
                {
                  text: '一定要出去玩',
                  result: 0
                },
                {
                  text: '終於可以好好休息',
                  result: 1
                }
              ]
            }
          ]
        },
        {
          questions: [
            {
              question: '比起一大群人的熱鬧派對，你更愛小小溫馨的私人派對',
              answers: [
                {
                  text: '是',
                  result: 0
                },
                {
                  text: '否',
                  result: 1
                }
              ]
            },
            {
              question: '總是因為更有趣事情或朋友的臨時邀約，而放棄原定的計劃',
              answers: [
                {
                  text: '常常如此',
                  result: 2
                },
                {
                  text: '幾乎沒有過',
                  result: 3
                }
              ]
            }
          ]
        },
        {
          questions: [
            {
              question: '參加活動及聚會時',
              answers: [
                {
                  text: '眾人圍繞著你，因你而充滿歡笑',
                  result: 0
                },
                {
                  text: '認識新朋友讓你感到開心',
                  result: 1
                }
              ]
            },
            {
              question: '參加活動及聚會時',
              answers: [
                {
                  text: '總是會有新朋友想要加你的LINE',
                  result: 2
                },
                {
                  text: '朋友圍繞著你，因你而充滿歡笑',
                  result: 3
                }
              ]
            },
            {
              question: '朋友找你訴苦時',
              answers: [
                {
                  text: '靜靜在旁，聽他訴說',
                  result: 4
                },
                {
                  text: '時常給出突發奇想的解決方式',
                  result: 5
                }
              ]
            },
            {
              question: '捷運停擺，誤點了! 跟朋友還有約你會…',
              answers: [
                {
                  text: '趕緊搜索腦中應變的Plan B',
                  result: 6
                },
                {
                  text: '慢慢來~ 朋友會諒解我的',
                  result: 7
                }
              ]
            }
          ]
        }
      ],
      total: 3
    }
  },
  methods: {
    nextQuestion(result) {
      // console.log(event.target)
      // console.log(this.minds.length)
      // console.log(result)
      let target = event.target
      target.classList.add('isActive')
      if(this.questionLevel === this.minds.length - 1) {
        console.log(result)
        this.$store.commit('changeState', result)
        this.$router.push({ path: '/result' })
      } else {
        this.isActive = false
        setTimeout(()=> {
          target.classList.remove('isActive')
          this.questionLevel = this.questionLevel + 1
          this.subQuestionLevel = result
          this.isActive = true
        }, 10)
      }
    }
  }
}
</script>

<style lang="scss">
  @import '../assets/scss/_base';
  .mind {
    @extend .gradient-block;
    &__wrap {
      @extend .gradient-block-wrap;
    }
    &__card-wrap {
      width: 100%;
      opacity: 1;
      transform: translate3d(0,0,0);
      transition: all 500ms ease;
      &.isActive {
        opacity: 1;
        transform: translate3d(0,0,0);
      }
    }
  }
  .fade-enter-active {
    opacity: 0;
    transform: translate3d(0,-5rem,0);
  }
  .fade-leave-active {
    opacity: 0;
  }
  .fade-enter {
    opacity: 1;
  }
  .fade-leave-to {
    opacity: 0;
    transform: translate3d(0,5rem,0);
  }
</style>